
.token-transition {
  transition: opacity .5s;
}
.token-enter-active, .token-leave-active {
  transition: opacity .5s
}
.token-enter, .token-leave-active {
  opacity: 0
}
.loader {
  z-index: 99999;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(3,3,3,0.3);
}



